.footer {
    background-color: black;
    color: white;
}
.footer__container {
    padding-top: 100px;
    padding-bottom: 30px;
    text-align: left;
}
.footer__links-groups {
    display: flex;
}
.footer__groups {
    display: flex;
    margin: 3rem 0;
    justify-content: space-between;
}
.footer__group {
    margin-right: 100px;
}
.footer__category {
    font-size: 1.2rem;
    margin-bottom: 1.5rem;
}
.footer__list {
    list-style: none;
}
.footer__link {
    color: #888;
    line-height: 200%;
    display: flex;
    align-items: center;
}
.footer__link:hover {
    color: #eee;
}
.footer__copy {
    border-top: 1px solid rgb(29, 29, 29);
    padding-top: 30px;
    text-align: center;
    color: #aaa;
}
.footer__icon {
    margin-right: 10px;
    color: var(--red-accent);
}
.footer__adress {
    text-align: right;
}
.footer__adress .footer__category {
    text-align: right;
}
.footer__location {
    display: flex;
    align-items: center;
    color: var(--red-accent);
    gap: 20px;
    font-size: 4rem;
}
.footer__location p {
    color: #888;
    font-size: 1rem;
    text-align: right;
    margin-right: 15px;
}
.footer__maps {
    color: white;
    display: inline-block;
    text-align: right;
    margin-top: 1rem;
    background-color: var(--red-accent);
    padding: 7px 10px;
}

@media screen and (max-width: 1050px) {
    .footer__logo {
        text-align: center;
    }
    .footer__groups {
        flex-direction: column;
        align-items: center;
        gap: 5rem;
    }
    .footer__links-groups {
        width: 100%;
        justify-content: space-around;
    }
    .footer__group {
        margin-right: 0;
    }
    .footer__adress .footer__category {
        text-align: center;
    }
    .footer__location-wrapper {
        display: flex;
        align-items: center;
        gap: 50px;
    }
    .footer__maps {
        margin: 0;
    }
}

@media screen and (max-width: 700px) {
    .footer__links-groups {
        padding: 0 20px;
        flex-direction: column;
        gap: 3rem;
    }
    .footer__location-wrapper {
        flex-direction: column;
        gap: 30px;
    }
}
