.scroll-top {
    position: fixed;
    bottom: -150px;
    right: 30px;
    background-color: var(--red-accent);
    z-index: 2000;
    color: white;
    font-size: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    box-shadow: var(--black-shadow-3);
    transition: 0.3s ease;
    cursor: pointer;
}
.scroll-top:hover {
    transform: scale(1.05);
}

.scroll-top--show {
    bottom: 30px;
}

@media screen and (max-width: 1000px) {
    .scroll-top {
        width: 50px;
        height: 50px;
        bottom: -150px;
        right: 10px;
        font-size: 2rem;
    }
    .scroll-top--show {
        bottom: 10px;
    }
}
