.contact__container {
    display: flex;
    gap: 150px;
    max-width: 1600px;
    border-radius: 25px;
    /*box-shadow: var(--black-shadow-1);
    background-color: #f7f7f7;*/
}
.contact__form {
    width: 50%;
    text-align: left;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.contact__form-title {
    font-size: 1.6rem;
    font-weight: 400;
    text-align: center;
    color: #333;
    margin-bottom: 1.5rem;
}
.contact__group {
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
}
.contact__name {
    display: flex;
    gap: 20px;
}
.contact__input,
.contact__message {
    width: 100%;
    border: none;
    border-bottom: 2px solid #ccc;
    border-radius: 5px 5px 0 0;
    padding: 10px;
    font-family: 'Poppins', sans-serif;
    font-size: 1.1rem;
    background-color: rgba(255, 255, 255, 0.2);
    /*background-color: transparent;*/
    outline: none;
    transition: 0.5s ease;
}
.contact__message {
    resize: none;
    min-height: 200px;
}
.contact__input:focus,
.contact__message:focus {
    border-bottom: 2px solid var(--red-accent);
    box-shadow: var(--black-shadow-1);
}
.contact__input:focus + .contact__label,
.contact__message:focus + .contact__label {
    color: var(--red-accent) !important;
}

.contact__label {
    font-size: 1.2rem;
    margin-bottom: 6px;
    color: #555;
    transition: 0.5s ease;
}
.contact__buttons {
    display: flex;
    justify-content: flex-end;
    margin-top: 2rem;
    gap: 30px;
}
.contact__btn {
    font-size: 1.5rem;
    width: 130px;
    height: 50px;
    padding: 5px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid black;
    color: black;
    opacity: 0.2;
    cursor: pointer;
    border-radius: 0;
    transition: 0.3s ease;
}
.contact__btn:hover {
    opacity: 0.4;
    border-radius: 5px;
    box-shadow: var(--black-shadow-2);
}
#contact__submit {
    border-color: var(--red-accent);
    opacity: 1;
    background-color: var(--red-accent);
    color: white;
}
#contact__submit:hover {
    border-radius: 5px;
    box-shadow: var(--black-shadow-2);
}

.contact__call {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.contact__numbers {
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.contact__phone {
    display: flex;
    gap: 20px;
    align-items: center;
}
.contact__link {
    color: white;
    background-color: var(--red-accent);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.contact__number {
    font-size: 1.5rem;
}
.contact__image {
    display: block;
    max-width: 70%;
    transform: scaleX(-1);
}

@media screen and (max-width: 1400px) {
    .contact__numbers {
        flex-direction: column;
        gap: 20px;
        align-items: center;
    }
    .contact__phone {
        width: 300px;
        display: flex;
        justify-content: space-between;
    }
    .contact__image {
        max-width: 90%;
    }
}

@media screen and (max-width: 1000px) {
    .contact__container {
        flex-direction: column;
        align-items: center;
        gap: 80px;
    }
    .contact__form,
    .contact__call {
        width: 80%;
    }
    .contact__numbers {
        flex-direction: row;
        margin-bottom: 3rem;
    }
    .contact__phone {
        width: auto;
    }
}

@media screen and (max-width: 830px) {
    .contact__form,
    .contact__call {
        width: 100%;
    }
    .contact__phone {
        width: 270px;
        gap: 10px;
    }
    .contact__numbers {
        flex-direction: column;
        margin-top: 1rem;
        margin-bottom: 3rem;
        gap: 15px;
        align-items: center;
    }
    .contact__name {
        flex-direction: column;
    }
    .contact__buttons {
        justify-content: center;
    }
}
